
import { computed, PropType } from "vue";

export default {
  props: {
    size: {
      type: String as PropType<string>,
      required: true,
    },
    color: {
      type: String as PropType<string>,
      required: false,
    },
  },
  setup(props: any) {
    const fillColor = computed(() => {
      if (props.color) {
        return props.color;
      }

      return "#ffffff";
    });

    return {
      fillColor,
    };
  },
};
